import { ref } from 'vue'
import { defineStore } from 'pinia'
import type { Department, OperationalBase } from '@/shared/types'

export const useDepartmentSelectStore = defineStore('departmentStore', () => {
  const selectedDepartmentId = ref<Department['id'] | null>(null)
  const selectedOperationalBaseId = ref<OperationalBase['id'] | null>(null)

  return {
    selectedDepartmentId, selectedOperationalBaseId,
  }
})
