import type { z } from 'zod'
import { objectPick } from '@antfu/utils'

import type { OperationalBase } from '@/shared/types'
import { normalizeDepartment } from '../department'
import type { OperationalBaseSchema } from '../schemas'

export function normalizeOperationalBase(
  operationalBase: z.infer<typeof OperationalBaseSchema>,
): OperationalBase {
  const {
    latitude,
    longitude,
    department,
  } = operationalBase

  return {
    ...objectPick(operationalBase, ['id', 'name']),
    latitude: +latitude,
    longitude: +longitude,
    department: normalizeDepartment(department),
  }
}
