import type { z } from 'zod'
import { faker as f } from '@faker-js/faker'
import { HttpResponse, http } from 'msw'

import type { Department } from '@/shared/types'
import { makeArrayMock } from '@/shared/lib/utils'

import { type Handlers, mockUrl } from '../lib'

import type { DepartmentSchema } from '../schemas'
import { normalizeDepartment } from './normalizers'
import { endpoints } from './api'

export function makeDepartmentSchemaMock(): z.infer<typeof DepartmentSchema> {
  return {
    id: f.number.int() as Department['id'],
    organisationId: f.number.int(),
    code: f.number.int(),
    name: f.company.name(),
    description: f.lorem.words(3),
    logoThumbnailSrc: f.image.avatar(),
  }
}

export const makeDepartmentMock = (): Department =>
  normalizeDepartment(makeDepartmentSchemaMock())

export const departmentHandlers: Handlers<typeof endpoints, 'Department'> = {
  DepartmentGetDepartments: (() => {
    const { url, method, schema } = endpoints.getDepartments

    return http[method](
      mockUrl(url),
      () => HttpResponse.json<z.infer<typeof schema>>(
        { departments: makeArrayMock(makeDepartmentSchemaMock, 2) },
      ),
    )
  })(),
}
