import { format, parse } from 'date-fns'
import { zonedTimeToUtc } from 'date-fns-tz'

import type { PDaysModel } from '@/shared/ui/PDays'

const ROUTE_DATE_FORMAT = 'yyyy-MM-dd'

const ROUTE_DATE_DELIMITER = '~'

export function dailyAgendaRouteParamToDate(param: string | null): PDaysModel {
  const datesParsed = param?.split(ROUTE_DATE_DELIMITER)
    .map(
      (d) => zonedTimeToUtc(
        parse(d, ROUTE_DATE_FORMAT, new Date()),
        'UTC',
      ),
    ) ?? []

  const start = datesParsed?.[0] ?? null
  const end = datesParsed?.[1] ?? null

  if (!end) return start

  return {
    start,
    end,
  }
}

export function dailyAgendaDateToRouteParam(date: PDaysModel): string | undefined {
  if (!date) return undefined

  if (date instanceof Date) return format(date, ROUTE_DATE_FORMAT)

  const { start, end } = date

  return `${format(start, ROUTE_DATE_FORMAT)}${ROUTE_DATE_DELIMITER}${format(end, ROUTE_DATE_FORMAT)}`
}
