import type { Department, OperationalBase } from '@/shared/types'

export type ProduceDepartmentAndBaseParams = {
  departmentId: Department['id'] | null
  departmentFirstBaseId: OperationalBase['id'] | null
  baseId: OperationalBase['id'] | null
  primaryDepartmentId: Department['id'] | null
  primaryDepartmentFirstBaseId: OperationalBase['id'] | null
  firstDepartmentId: Department['id'] | null
  firstDepartmentsBaseId: OperationalBase['id'] | null
  showAll: boolean
  isBaseRequired: boolean
}
export function produceDepartmentAndBase(
  {
    departmentId,
    departmentFirstBaseId,
    baseId,
    primaryDepartmentId,
    primaryDepartmentFirstBaseId,
    showAll,
    firstDepartmentId,
    firstDepartmentsBaseId,
    isBaseRequired,
  }: ProduceDepartmentAndBaseParams,
): {
    departmentId: Department['id'] | null
    baseId: OperationalBase['id'] | null
  } {
  if (departmentId) return {
    departmentId,
    baseId: baseId ?? (isBaseRequired ? departmentFirstBaseId : null),
  }

  if (showAll) {
    return {
      departmentId: null,
      baseId: null,
    }
  }

  if (primaryDepartmentId) {
    return {
      departmentId: primaryDepartmentId,
      baseId: isBaseRequired ? primaryDepartmentFirstBaseId : null,
    }
  }

  return {
    departmentId: firstDepartmentId,
    baseId: isBaseRequired ? firstDepartmentsBaseId : null,
  }
}

type MenuItem = { title: string; isActive: boolean; onClick: () => void }

type DepartmentAndBases = Pick<Department, 'id' | 'name'> & {
  bases: Pick<OperationalBase, 'id' | 'name'>[]
}

export type ProduceDepartmentSelectMenuItemsParams = {
  departmentsAndBases: DepartmentAndBases[]
  selectedDepartmentId: Department['id'] | null
  selectedBaseId: OperationalBase['id'] | null
  showAll: boolean
  showBases: boolean
  isBaseRequired: boolean
  selectDepartment: (departmentId: Department['id'] | null) => void
  selectBase: (departmentId: Department['id'], baseId: OperationalBase['id']) => void
}
export function produceDepartmentSelectMenuItems(
  {
    departmentsAndBases,
    selectedDepartmentId,
    selectedBaseId,
    showAll,
    showBases,
    isBaseRequired,
    selectDepartment,
    selectBase,
  }: ProduceDepartmentSelectMenuItemsParams,
): MenuItem[] {
  const items: MenuItem[] = []

  if (showAll) {
    items.push({
      title: 'All departments',
      isActive: selectedDepartmentId === null,
      onClick: () => selectDepartment(null),
    })
  }

  for (const { id, name, bases } of departmentsAndBases) {
    const isSingleBase = bases.length === 1

    if (!isBaseRequired || isSingleBase) {
      const base = bases[0]!

      items.push({
        title: name,
        isActive: id === selectedDepartmentId && (
          !selectedBaseId || (isBaseRequired && isSingleBase)
        ),
        onClick: () => {
          if (isBaseRequired) {
            selectBase(id, base.id)
            return
          }

          selectDepartment(id)
        },
      })
    }

    if (showBases && (!isSingleBase || !isBaseRequired)) {
      for (const { id: baseId, name: baseName } of bases) {
        items.push({
          title: `${name} - ${baseName}`,
          isActive: baseId === selectedBaseId,
          onClick: () => selectBase(id, baseId),
        })
      }
    }
  }

  return items
}
