import { defineStore } from 'pinia'
import { ref } from 'vue'
import type { IAction } from '@/shared/types/api/IAction'
import type { IDataFilters } from '@/shared/types/inputs/IDataFilters'

export const useUiStore = defineStore('ui', () => {
  const actionsInProgress = ref<IAction[]>([])
  const dynamicIndexesInProgress = ref<number[]>([])
  const actionsInError = ref<IAction[]>([])
  const actionsInSuccess = ref<IAction[]>([])

  const storeFilterState = ref<IDataFilters>({
    selectedDepartment: null,
    selectedOperationalBase: null,
    selectedRole: null,
    selectedMonth: '',
    selectedDate: '',
  })

  const navDrawerIsOpen = ref(false)
  function toggleNavDrawerIsOpen() {
    navDrawerIsOpen.value = !navDrawerIsOpen.value
  }

  function addToActionsInProgress(action: IAction, dynamicIndex?: number) {
    if (!actionIsInProgress(action)) {
      actionsInProgress.value.push(action)
    }
    // dynamicIndex can be 0, which would evaluate to false if we
    // just did an if (dynamicIndex) check.
    if (Number.isInteger(dynamicIndex) && !dynamicIndexesInProgress.value.includes(dynamicIndex!)) {
      dynamicIndexesInProgress.value.push(dynamicIndex!)
    }
  }

  function removeFromActionsInProgress(action: IAction, dynamicIndex?: number) {
    actionsInProgress.value = actionsInProgress.value.filter((a) => a.id !== action.id)
    if (Number.isInteger(dynamicIndex)) {
      dynamicIndexesInProgress.value = dynamicIndexesInProgress.value.filter((i) => i !== dynamicIndex)
    }
  }

  function actionIsInProgress(action: IAction, dynamicIndex?: number) {
    // dynamicIndex can be 0, which would evaluate to false if we
    // just did an if (dynamicIndex) check.
    if (Number.isInteger(dynamicIndex)) {
      return actionsInProgress.value.some((a) => a.id === action.id)
        && dynamicIndexesInProgress.value.includes(dynamicIndex!)
    }
    else {
      return actionsInProgress.value.some((a) => a.id === action.id)
    }
  }

  function actionIsInError(action: IAction, dynamic?: boolean) {
    if (dynamic) {
      return actionsInError.value.some((a) => a.id.includes(action.id))
    }
    return actionsInError.value.some((a) => a.id === action.id)
  }

  function actionIsInSuccess(action: IAction, dynamic?: boolean) {
    if (dynamic) {
      return actionsInSuccess.value.some((a) => a.id.includes(action.id))
    }
    return actionsInSuccess.value.some((a) => a.id === action.id)
  }

  function addToActionsInError(action: IAction, e: any) {
    /**
     * We check to see if 'e' is a response from the server
     * with an error message. If it is, we replace the default
     * error message with the one from the server.
     */
    if (e.response) {
      action.errorMessage = e.response.data.message
    }

    if (!actionIsInError(action)) {
      actionsInError.value.push(action)
    }
  }

  function addToActionsInSuccess(action: IAction, successMessage: string | null = null) {
    if (successMessage) {
      action.successMessage = successMessage
    }

    if (!actionIsInSuccess(action)) {
      actionsInSuccess.value.push(action)
    }
  }

  function resetActionsInError() {
    actionsInError.value = []
  }

  function resetActionsInSuccess() {
    actionsInSuccess.value = []
  }

  function resetActionsInProgress() {
    actionsInProgress.value = []
  }

  function resetUi() {
    resetActionsInError()
    resetActionsInSuccess()
    resetActionsInProgress()
  }

  function getActionErrorMessage(action: IAction, dynamic?: boolean) {
    if (dynamic) {
      return actionsInError.value.find((a) => a.id.includes(action.id))?.errorMessage
    }
    return actionsInError.value.find((a) => a.id === action.id)?.errorMessage
  }

  function getActionSuccessMessage(action: IAction, dynamic?: boolean) {
    if (dynamic) {
      return actionsInSuccess.value.find((a) => a.id.includes(action.id))?.successMessage
    }
    return actionsInSuccess.value.find((a) => a.id === action.id)?.successMessage
  }

  function storeSetFilterState(state: IDataFilters) {
    storeFilterState.value = state
  }

  return {
    addToActionsInProgress,
    removeFromActionsInProgress,
    actionIsInProgress,
    actionIsInError,
    actionIsInSuccess,
    addToActionsInError,
    addToActionsInSuccess,
    resetActionsInError,
    resetActionsInSuccess,
    resetActionsInProgress,
    resetUi,
    actionsInProgress,
    dynamicIndexesInProgress,
    actionsInError,
    actionsInSuccess,
    navDrawerIsOpen,
    toggleNavDrawerIsOpen,
    getActionErrorMessage,
    getActionSuccessMessage,
    storeSetFilterState,
    storeFilterState,
  }
})
