import { storeToRefs } from 'pinia'
import { useUi } from '@/shared/lib/composables/useUi'
import type { IAction } from '@/shared/types/api/IAction'
import { useApiClient } from '@/shared/api/useApiClient'
import { useAlertsStore } from '@/shared/api/stores/alerts'
import type { IAlerts } from '@/shared/types/alerts/IAlerts'

export function useAlerts() {
  const { apiClient } = useApiClient()
  const {
    addToActionsInProgress,
    removeFromActionsInProgress,
    processActionError,
    resetActionsInError,
  } = useUi()

  const {
    setAlertsIndex,
  } = useAlertsStore()

  const { alertsIndex } = storeToRefs(useAlertsStore())

  const alertActions = {
    getAllAlerts: {
      id: 'getAllAlerts',
      successMessage: 'Alerts retrieved successfully.',
      errorMessage: 'There was a problem retrieving alerts.',
    } as IAction,
  }

  const fetchAndStoreAlertsIndex = async () => {
    resetActionsInError()
    addToActionsInProgress(alertActions.getAllAlerts)

    try {
      const rsp = await apiClient.get('/api/alerts')
      setAlertsIndex(rsp.data.alerts)

      removeFromActionsInProgress(alertActions.getAllAlerts)
    }
    catch (e: any) {
      console.log(e)
      processActionError(alertActions.getAllAlerts, e)
    }
  }

  const getAllAlertsFromStore = (): IAlerts | null => {
    if (!alertsIndex.value) return null
    return alertsIndex.value
  }

  return {
    fetchAndStoreAlertsIndex,
    getAllAlertsFromStore,
    alertActions,
  }
}
