import { z } from 'zod'
import { type ApiEndpointsAndSchemas, client } from '../lib'

import { DepartmentSchema } from '../schemas'
import { normalizeDepartment } from './normalizers'

export const endpoints = {
  getDepartments: {
    url: '/departments',
    method: 'get',
    schema: z.object({ departments: z.array(DepartmentSchema) }),
  },
} satisfies ApiEndpointsAndSchemas

export { endpoints as departmentEndpoints }

export async function getDepartments() {
  const { url, method, schema } = endpoints.getDepartments

  const data = await client[method](url, schema)

  return data.departments.map(normalizeDepartment)
}
