<script setup lang="ts">
import { computed, toRefs, watch } from 'vue'
import { storeToRefs } from 'pinia'
import { until, useArrayMap } from '@vueuse/core'
import { not } from '@vueuse/math'
import { objectPick } from '@antfu/utils'
import { Menu, MenuButton, MenuItem, MenuItems } from '@headlessui/vue'
import { ChevronDownIcon } from '@heroicons/vue/20/solid'

// import { useRoute } from 'vue-router'
import type { Department, OperationalBase } from '@/shared/types'
import { useUser } from '@/shared/api/useUser'
import { useOperationalBasesByDepartmentIds } from '@/shared/api/operational-base'

import { useDepartmentSelectStore } from './model'
import { produceDepartmentAndBase, produceDepartmentSelectMenuItems } from './lib'
// import { useAuth } from '@/shared/api/useAuth'

export interface Props {
  showAllDepartments?: boolean
  showBases?: boolean
  baseRequired?: boolean
}

const props = defineProps<Props>()
const { showAllDepartments, showBases, baseRequired } = toRefs(props)

const { user } = useUser()
// const { userHasSomePermissionsInDepartment } = useAuth()
// const route = useRoute()

const departments = computed(() => {
  const value = user.value?.departments ?? []

  // TODO: Remove type cast after api migration
  return value as (Omit<typeof value[number], 'id'> & { id: Department['id'] })[]
})

const { data: operationalBases } = useOperationalBasesByDepartmentIds({
  departmentIds: useArrayMap(departments, (d) => d.id),
})

const departmentsWithPermissions = computed(() => {
  return departments.value

  // TODO: Deal with permissions
  // const permission = route.meta.permission

  // if (permission) {
  //   return departments.value.filter((d) => {
  //     return userHasSomePermissionsInDepartment([permission], d)
  //   })
  // }

  // return []
})

const departmentsAndOperationalBases = computed(
  () => departmentsWithPermissions.value.map((d) => ({
    ...d,
    operationalBases: operationalBases.value?.filter(
      (b) => b.department?.id === d.id,
    ) ?? [],
  }),
  ),
)

const { selectedDepartmentId, selectedOperationalBaseId } = storeToRefs(
  useDepartmentSelectStore(),
)

const selectDepartment = (departmentId: Department['id'] | null) => {
  selectedDepartmentId.value = departmentId
  selectedOperationalBaseId.value = null
}

const selectOperationalBase = (
  departmentId: Department['id'],
  operationalBaseId: OperationalBase['id'],
) => {
  selectedDepartmentId.value = departmentId
  selectedOperationalBaseId.value = operationalBaseId
}

const selectedDepartment = computed(
  () => departmentsAndOperationalBases.value.find(
    (d) => d.id === selectedDepartmentId.value,
  ),
)
const selectedOperationalBase = computed(() => operationalBases.value?.find(
  (base) => base.id === selectedOperationalBaseId.value,
))

const isSelectedDepartmentNull = not(selectedDepartment)

watch([
  departments,
  operationalBases,
  showAllDepartments,
  baseRequired,
  isSelectedDepartmentNull,
], async () => {
  await until(() => departments.value.length && operationalBases.value?.length).toBeTruthy()

  const primaryDepartment = departmentsAndOperationalBases.value.find((d) => d.primary)
  const firstDepartment = departmentsAndOperationalBases.value[0]

  const { departmentId, baseId } = produceDepartmentAndBase({
    departmentId: !isSelectedDepartmentNull.value ? selectedDepartmentId.value : null,
    departmentFirstBaseId: selectedDepartment.value?.operationalBases[0]?.id ?? null,
    baseId: selectedOperationalBaseId.value,
    primaryDepartmentId: primaryDepartment?.id ?? null,
    primaryDepartmentFirstBaseId: primaryDepartment?.operationalBases[0]?.id ?? null,
    firstDepartmentId: firstDepartment?.id ?? null,
    firstDepartmentsBaseId: firstDepartment?.operationalBases[0]?.id ?? null,
    showAll: showAllDepartments.value,
    isBaseRequired: baseRequired.value,
  })

  selectedDepartmentId.value = departmentId
  selectedOperationalBaseId.value = baseId
})

const shouldShowSelectedBaseName = computed(() => {
  return (
    showBases.value
    && selectedOperationalBaseId.value
    && (selectedDepartment.value?.operationalBases ?? [])?.length > 1
  )
})

const menuItems = computed(() => produceDepartmentSelectMenuItems({
  departmentsAndBases: departmentsAndOperationalBases.value.map((d) => ({
    ...objectPick(d, ['id', 'name']),
    bases: d.operationalBases,
  })),
  selectedDepartmentId: selectedDepartmentId.value,
  selectedBaseId: selectedOperationalBaseId.value,
  showAll: showAllDepartments.value,
  showBases: showBases.value,
  isBaseRequired: baseRequired.value,
  selectDepartment,
  selectBase: selectOperationalBase,
}))
</script>

<template>
  <Menu
    v-if="departmentsAndOperationalBases.length"
    class="relative"
    as="div"
  >
    <MenuButton class="flex cursor-pointer items-center gap-1 text-blue-500">
      <div v-if="showAllDepartments && !selectedDepartmentId">
        All Departments
      </div>

      {{ selectedDepartment?.name }}
      <span v-if="shouldShowSelectedBaseName">
        - {{ selectedOperationalBase?.name }}
      </span>
      <ChevronDownIcon class="h-5 w-5" />
    </MenuButton>

    <Transition
      enter-active-class="transition duration-100 ease-out"
      enter-from-class="transform scale-95 opacity-0"
      enter-to-class="transform scale-100 opacity-100"
      leave-active-class="transition duration-75 ease-in"
      leave-from-class="transform scale-100 opacity-100"
      leave-to-class="transform scale-95 opacity-0"
    >
      <MenuItems
        class="
        absolute z-20 mt-2 w-64 rounded-lg border bg-white text-gray-400
        shadow-lg
        "
      >
        <MenuItem
          v-for="{ title, isActive, onClick } in menuItems"
          :key="title"
          as="button"
          class="
          w-full cursor-pointer flex-col gap-1 p-1 px-5 py-2 text-sm font-medium
          hover:bg-gray-100"
          :class="[isActive && 'text-blue-500']"
          @click="onClick"
        >
          {{ title }}
        </MenuItem>
      </MenuItems>
    </Transition>
  </Menu>
</template>
