import type { Component } from 'vue'
import type { RouteRecordRaw } from 'vue-router/auto'
import type { ParamValue, RouteRecordInfo } from 'unplugin-vue-router'

// TODO: Update imports after FSD
import type { Middleware } from '@/shared/types/enums/middleware/middleware'
import type { AppPermissions } from '@/shared/types/enums/permissions/permissions'

type RouteMapValue<K extends string> = Record<K, Record<string, string> | null>

export type AbstractRouteMap<
  K extends string = string, T extends RouteMapValue<K> = RouteMapValue<K>,
> = T

export type RouteMapToNamedMap<RoutesMap extends AbstractRouteMap> = {
  [K in keyof RoutesMap]: K extends string
    ? RouteRecordInfo<
        K,
        K,
        { [Q in keyof RoutesMap[K]]: ParamValue<true> },
        { [Q in keyof RoutesMap[K]]: ParamValue<false> }
      >
    : never
}

type Lazy<T> = () => Promise<T>

// TODO: Simplify. Probably flat structure could help.
export type CustomRouteRecord<
  TRouteMap extends AbstractRouteMap = AbstractRouteMap, TKey extends keyof TRouteMap = keyof TRouteMap, TCompProps = any,
> = Omit<RouteRecordRaw, 'component' | 'props' | 'meta' | 'children'> & {
  name: TKey
  component: Lazy<{ default: Component<TCompProps> }>
  // TODO: Think how to keep meta agreed with RouteMeta interface
  meta?: {
    middleware?: Middleware[]
    permission?: AppPermissions
    layout?: Component | null
    departmentSelect?: {
      show?: boolean
      all?: boolean
      bases?: boolean
      baseRequired?: boolean
    }
  }

  children?: CustomRouteRecord[]

  components?: never
} & (TRouteMap[TKey] extends Record<string, string> ? {
  props: (args: { params: { [Z in keyof TRouteMap[TKey]]: string } }) => TCompProps
} : {})

// TODO: Deal with formatting
export const makeDefineRouteFn = <RouteMap extends AbstractRouteMap>() => <
TKey extends keyof RouteMap, TComponentProps,
>(route: CustomRouteRecord<RouteMap, TKey, TComponentProps>) => route

type MapIDtoPropsFn = (route: { params: { id: string } }) => { id: number }

export const mapIDtoProps: MapIDtoPropsFn = ({ params: { id } }) => ({ id: +id })

export function paramsToNumbers<
  T extends Record<string, string>,
>({ params }: { params: T }) {
  return Object.fromEntries(
    Object.entries(params).map(([key, value]) => [key, +value]),
  ) as Record<keyof T, number>
}
