import type { z } from 'zod'
import { objectPick } from '@antfu/utils'

import type { Department } from '@/shared/types'
import type { DepartmentSchema } from '../schemas'

export function normalizeDepartment(
  department: z.infer<typeof DepartmentSchema>,
): Department {
  const {
    organisationId,
    logoThumbnailSrc,
  } = department

  return {
    ...objectPick(department, ['id', 'name', 'code', 'description']),
    organizationId: organisationId,
    logo: logoThumbnailSrc ?? null,
  }
}
