import { z } from 'zod'
import type { Department } from '@/shared/types'
import { type ApiEndpointsAndSchemas, type ToKeyParams, client } from '../lib'

import { OperationalBaseSchema } from '../schemas'
import { normalizeOperationalBase } from './normalizers'

export const endpoints = {
  byDepartmentIds: {
    url: '/operational-bases/get-by-departments',
    method: 'post',
    schema: z.object({ operationalBases: z.array(OperationalBaseSchema) }),
  },
  byDepartmentId: {
    url: '/operational-bases/get-by-departments',
    method: 'post',
    schema: z.object({ operationalBases: z.array(OperationalBaseSchema) }),
  },
} satisfies ApiEndpointsAndSchemas

export { endpoints as operationalBaseEndpoints }

export type GetOperationalBasesByDepartmentIdsParams = {
  departmentIds: Department['id'][]
}
export type GetOperationalBasesByDepartmentIdsKeyParams = ToKeyParams<
GetOperationalBasesByDepartmentIdsParams
>
export async function getOperationalBasesByDepartmentIds(
  { departmentIds }: GetOperationalBasesByDepartmentIdsParams,
) {
  const { url, method, schema } = endpoints.byDepartmentIds

  const data = await client[method](url, { departmentIds }, schema)

  return data.operationalBases.map(normalizeOperationalBase)
}

export type GetOperationalBasesByDepartmentIdParams = {
  departmentId: Department['id']
}
export type GetOperationalBasesByDepartmentIdKeyParams = ToKeyParams<
  GetOperationalBasesByDepartmentIdParams
>
export async function getOperationalBasesByDepartmentId(
  { departmentId }: GetOperationalBasesByDepartmentIdParams,
) {
  const { url, method, schema } = endpoints.byDepartmentId

  const data = await client[method](url, { departmentIds: [departmentId] }, schema)

  return data.operationalBases.map(normalizeOperationalBase)
}
