<script setup lang="ts">
import { computed, ref } from 'vue'
import { storeToRefs } from 'pinia'
import { toRefs, useLocalStorage, whenever } from '@vueuse/core'
import { useRouter } from 'vue-router/auto'
import { addDays, differenceInDays, startOfToday } from 'date-fns'
import { Cog6ToothIcon } from '@heroicons/vue/24/outline'

import { useDepartmentSelectStore } from '@/features/department-select'
import { DepartureList } from '@/widgets/departure-list'

import { Routes } from '@/shared/lib/router'

import { PToggle } from '@/shared/ui/PToggle'
import { PRadio } from '@/shared/ui/PRadio'
import { PDays, type PDaysModel } from '@/shared/ui/PDays'
import { PPopover, PPopoverButton, PPopoverPanel } from '@/shared/ui/PPopover'

import MainContent from '@/shared/ui/layout/MainContent.vue'

import { dailyAgendaDateToRouteParam } from './lib'

const props = defineProps<{ date: PDaysModel }>()
const { date } = toRefs(props)

const router = useRouter()

const routeDate = computed({
  get: () => date.value,
  set: (value) => router.push({
    name: Routes.DailyAgenda,
    params: { date: dailyAgendaDateToRouteParam(value) },
  }),
})

const shouldShowClosedOrCancelledDepartures = ref(false)
const isCompactView = useLocalStorage('daily-agenda-is-compact-view', false)

const {
  selectedDepartmentId: departmentId,
  selectedOperationalBaseId: operationalBaseId,
} = storeToRefs(useDepartmentSelectStore())

whenever(departmentId, () => {
  if (operationalBaseId.value) {
    operationalBaseId.value = null
  }
})

const UPCOMING_DAYS_COUNT = 5

const days = computed(() => {
  const value = date.value

  if (!value) {
    const today = startOfToday()

    return [
      today,
      ...Array.from(
        { length: UPCOMING_DAYS_COUNT }, (_, idx) => addDays(today, idx + 1),
      ),
    ]
  }

  if (!(value instanceof Date)) {
    const { start, end } = value

    return [
      start,
      ...Array.from(
        { length: differenceInDays(end, start) }, (_, idx) => addDays(start, idx + 1),
      ),
    ]
  }

  return [value]
})
</script>

<template>
  <MainContent main-content-title="Daily Agenda">
    <div class="flex flex-col gap-y-6">
      <PDays v-model="routeDate" class="w-fit" />

      <div class="flex w-full items-center justify-end">
        <PPopover :float="{ placement: 'bottom-end' }">
          <PPopoverButton
            class="flex items-center justify-center gap-1 text-lg focus:outline-none"
          >
            <Cog6ToothIcon class="h-6 w-6" />
          </PPopoverButton>

          <PPopoverPanel class="p-0">
            <div class="flex h-full flex-col">
              <div class="flex flex-col gap-1 border-b border-gray-200 p-3">
                <span class="text-xs text-gray-400">
                  Departures:
                </span>
                <PToggle
                  v-model="shouldShowClosedOrCancelledDepartures"
                  icon
                  class="text-xs text-gray-400"
                >
                  Show ongoing departures
                </PToggle>
              </div>

              <div class="flex flex-col gap-2 p-3">
                <span class="text-xs text-gray-400">
                  Density:
                </span>
                <PRadio v-model="isCompactView" :value="false">
                  Default
                </PRadio>
                <PRadio v-model="isCompactView" :value="true">
                  Compact
                </PRadio>
              </div>
            </div>
          </PPopoverPanel>
        </PPopover>
      </div>
    </div>

    <div :class="!isCompactView && 'space-y-4'">
      <DepartureList
        v-for="day in days"
        :key="day.toISOString()"
        :date="day"
        :department-id="departmentId"
        :operational-base-id="operationalBaseId"
        :show-closed-or-cancelled="shouldShowClosedOrCancelledDepartures"
        :compact="isCompactView"
        :show-fallback="!date"
      />
    </div>
  </MainContent>
</template>
