import type { z } from 'zod'
import { faker as f } from '@faker-js/faker'
import { HttpResponse, http } from 'msw'

import type { OperationalBase } from '@/shared/types'
import { makeArrayMock } from '@/shared/lib/utils'

import { type Handlers, mockUrl } from '../lib'
import { makeDepartmentSchemaMock } from '../department/mock'

import type { OperationalBaseSchema } from '../schemas'
import { normalizeOperationalBase } from './normalizers'
import { endpoints } from './api'

export function makeOperationalBaseSchemaMock(
): z.infer<typeof OperationalBaseSchema> {
  return {
    id: f.number.int() as OperationalBase['id'],
    name: f.lorem.words(3),
    latitude: f.location.latitude().toString(),
    longitude: f.location.longitude().toString(),
    department: makeDepartmentSchemaMock(),
  }
}

export const makeOperationalBaseMock = (): OperationalBase =>
  normalizeOperationalBase(makeOperationalBaseSchemaMock())

export const operationalBasesHandlers: Handlers<typeof endpoints, 'OperationalBases'> = {
  OperationalBasesByDepartmentIds: (() => {
    const { url, method, schema } = endpoints.byDepartmentIds

    return http[method](
      mockUrl(url),
      () => HttpResponse.json<z.infer<typeof schema>>(
        { operationalBases: makeArrayMock(makeOperationalBaseSchemaMock) },
      ),
    )
  })(),
  OperationalBasesByDepartmentId: (() => {
    const { url, method, schema } = endpoints.byDepartmentId

    return http[method](
      mockUrl(url),
      () => HttpResponse.json<z.infer<typeof schema>>(
        { operationalBases: makeArrayMock(makeOperationalBaseSchemaMock) },
      ),
    )
  })(),
}
