import { type Preview, setup } from '@storybook/vue3'
// @ts-expect-error - msw-storybook-addon is under development
import { initialize as initializeMSW, mswDecorator } from 'msw-storybook-addon'
import { vueRouter } from 'storybook-vue3-router'

import { applyProviders } from '@/app/providers'
import '@/app/styles/index.css'

import { routes } from '@/pages'

import { API_URL } from '@/shared/config'
import { handlers } from '@/shared/api/handlers'

initializeMSW({
  // TODO: Remove any when msw-storybook-addon is updated
  onUnhandledRequest: (req: any) => {
    if (req.url.startsWith(API_URL)) {
      console.error(`Unhandled ${req.method} request to ${req.url}`)
    }
  },

})

setup((app) => {
  applyProviders(app)

  app.config.globalProperties.$Routes = {} as any
  app.component('RouterView', () => undefined)
})

const preview: Preview = {
  parameters: {
    actions: { argTypesRegex: '^on[A-Z].*' },
    backgrounds: {
      default: 'light',
      values: [
        { name: 'light', value: 'white' },
        { name: 'black', value: '#000' },
      ],
    },
    controls: {
      matchers: {
        color: /(background|color)$/i,
        date: /Date$/i,
      },
    },
    options: {
      storySort: {
        order: ['Shared', 'Entities', 'Features', 'Widgets', 'Pages'],
      },
    },
    layout: 'centered',
    html: {
      prettier: {
        tabWidth: 2,
      },
      highlighter: {
        showLineNumbers: true,
        wrapLines: true,
      },
    },
    msw: { handlers },
  },
  decorators: [
    mswDecorator,
    // @ts-expect-error - because of custom route types
    vueRouter(routes),
  ],
}

export default preview
