import { storeToRefs } from 'pinia'
import { useUiStore } from '@/shared/lib/composables/ui'
import type { IAction } from '@/shared/types/api/IAction'

export function useUi() {
  const {
    addToActionsInProgress,
    removeFromActionsInProgress,
    actionIsInProgress,
    actionIsInError,
    actionIsInSuccess,
    addToActionsInSuccess,
    addToActionsInError,
    resetActionsInError,
    resetActionsInSuccess,
    resetActionsInProgress,
    resetUi,
    toggleNavDrawerIsOpen,
    getActionErrorMessage,
    getActionSuccessMessage,
  } = useUiStore()

  const { navDrawerIsOpen } = storeToRefs(useUiStore())

  const processActionError = (action: IAction, e: unknown) => {
    addToActionsInError(action, e)
    removeFromActionsInProgress(action)
  }

  const processActionSuccess = (action: IAction, successMessage: string | null = null) => {
    addToActionsInSuccess(action, successMessage)
    removeFromActionsInProgress(action)
  }

  /**
   * This methods takes a form element and a callback function.
   * It will validate the form and if it is valid, it will call the callback function.
   * If the form is invalid, it will scroll to the first invalid input and report the error.
   * ### IMPORTANT ###
   * This method is only meant to be used with forms in the right drawer.
   * It allows for the sticky header, which is 88px high, to be taken into account when
   * scrolling to the first invalid input.
   * This is fine, as the majority of CRUD operations will take place in the
   * RightDrawer component for this app.
   */
  const validateForm = async <T>(formElement: HTMLFormElement | null, callback: Function): Promise<T | void> => {
    if (formElement) {
      const inputs = formElement.querySelectorAll('input, select, textarea')
      let isValid = true

      const rightDrawer = document.querySelector('#right-drawer')

      if (rightDrawer) {
        for (let i = 0; i < inputs.length; i++) {
          const input = inputs[i] as HTMLInputElement
          if (!input.checkValidity()) {
            const scrollToYPosition = input.getBoundingClientRect().top - rightDrawer.getBoundingClientRect().top + rightDrawer.scrollTop - 88
            rightDrawer.scrollTo({
              top: scrollToYPosition,
            })
            isValid = false
            setTimeout(() => {
              input.reportValidity()
            }, 50)
            break // Break out of the loop on the first invalid input
          }
        }
      }
      else {
        for (let i = 0; i < inputs.length; i++) {
          const input = inputs[i] as HTMLInputElement
          if (!input.checkValidity()) {
            isValid = false
            input.reportValidity()
            break // Break out of the loop on the first invalid input
          }
        }
      }

      if (isValid) {
        return await callback()
      }
    }
  }

  const uiActions = {
    bootingApp: {
      id: 'bootingApp',
      successMessage: 'Successfully booted app.',
      errorMessage: 'There was an error booting the app.',
    } as IAction,
  }

  return {
    addToActionsInProgress,
    addToActionsInSuccess,
    removeFromActionsInProgress,
    actionIsInProgress,
    actionIsInError,
    actionIsInSuccess,
    addToActionsInError,
    resetActionsInError,
    resetActionsInSuccess,
    resetActionsInProgress,
    resetUi,
    processActionError,
    processActionSuccess,
    validateForm,
    toggleNavDrawerIsOpen,
    getActionErrorMessage,
    getActionSuccessMessage,
    navDrawerIsOpen,
    uiActions,
  }
}
