import { storeToRefs } from 'pinia'
import { useUserStore } from '@/shared/api/stores/user'
import { useUi } from '@/shared/lib/composables/useUi'
import type { IAction } from '@/shared/types/api/IAction'
import { useApiClient } from '@/shared/api/useApiClient'
import { useAlerts } from '@/shared/api/useAlerts'
import type { CreateUserDTO, EditUserDTO, UpdateUserPasswordDTO } from '@/shared/types/user/UserDTOs'
import type { IUser } from '@/shared/types/user/IUser'
import { useUiStore } from '@/shared/lib/composables/ui'
import type { Shift } from '../types/user/IShift'

export function useUser() {
  const { apiClient } = useApiClient()
  const {
    addToActionsInProgress,
    removeFromActionsInProgress,
    processActionError,
    processActionSuccess,
    resetActionsInError,
    resetActionsInSuccess,
    getActionSuccessMessage,
  } = useUi()
  const { user } = storeToRefs(useUserStore())
  const {
    storeSetUser,
    storeSetUsers,
    storeAddUser,
    storeResetUsers,
  } = useUserStore()

  const { storeFilterState } = storeToRefs(useUiStore())
  const { fetchAndStoreAlertsIndex } = useAlerts()

  const userActions = {
    createUser: {
      id: 'createUser',
      successMessage: 'User created successfully.',
      errorMessage: 'There was a problem creating the user.',
    } as IAction,
    getAllUsers: {
      id: 'getAllUsers',
      successMessage: 'Users successfully retrieved.',
      errorMessage: 'There was a problem retrieving the users.',
    } as IAction,
    getAllAbilities: {
      id: 'getAllAbilities',
      successMessage: 'Abilities successfully retrieved.',
      errorMessage: 'There was a problem retrieving the abilities.',
    } as IAction,
    getAllRoles: {
      id: 'getAllRoles',
      successMessage: 'Roles successfully retrieved.',
      errorMessage: 'There was a problem retrieving the roles.',
    } as IAction,
    getAllPermissions: {
      id: 'getAllPermissions',
      successMessage: 'Permissions successfully retrieved.',
      errorMessage: 'There was a problem retrieving the permissions.',
    } as IAction,
    getUserById: {
      id: 'getUserById',
      successMessage: 'User successfully retrieved.',
      errorMessage: 'There was a problem retrieving the user.',
    } as IAction,
    editUser: {
      id: 'editUser',
      successMessage: 'User successfully updated.',
      errorMessage: 'There was a problem updating the user.',
    } as IAction,
    changeUserPassword: {
      id: 'changeUserPassword',
      successMessage: 'Password successfully updated.',
      errorMessage: 'There was a problem updating the password.',
    } as IAction,
    updateEmploymentHistory: {
      id: 'updateEmploymentHistory',
      successMessage: 'Employment history successfully updated.',
      errorMessage: 'There was a problem updating the employment history.',
    } as IAction,
    addEmploymentHistory: {
      id: 'addEmploymentHistory',
      successMessage: 'Employment history successfully updated.',
      errorMessage: 'There was a problem updating the employment history.',
    } as IAction,
    getUpcomingShifts: {
      id: 'getUpcomingShifts',
      successMessage: 'Upcoming user shifts successfully retrieved.',
      errorMessage: 'There was a problem retrieving the upcoming user shifts.',
    } as IAction,
  }

  const createUser = async (createUserDTO: CreateUserDTO) => {
    /**
     * We clear the actions in error so that any error response from
     * a previous attempt to create a user is cleared.
     */
    resetActionsInError()
    resetActionsInSuccess()
    addToActionsInProgress(userActions.createUser)

    try {
      const rsp = await apiClient.post('/api/user', createUserDTO)

      const userDepartmentIds = rsp.data.user.departments.map((department: any) => department.id)
      if (userDepartmentIds.includes(storeFilterState.value.selectedDepartment?.id)) {
        storeAddUser(rsp.data.user)
      }
      processActionSuccess(userActions.createUser)
    }
    catch (e: unknown) {
      console.log(e)
      processActionError(userActions.createUser, e)
    }
  }

  const fetchAndStoreUsersIndex = async (
    selectedDepartmentId: number,
    includeUsersNotCurrentlyEmployed = false,
  ) => {
    resetActionsInError()
    addToActionsInProgress(userActions.getAllUsers)

    try {
      const url = includeUsersNotCurrentlyEmployed
        ? '/api/users'
        : '/api/current-users'
      const rsp = await apiClient.get(url, {
        params: {
          selectedDepartmentId,
        },
      })
      storeSetUsers(rsp.data.users)

      removeFromActionsInProgress(userActions.getAllUsers)
    }
    catch (e: unknown) {
      console.log(e)
      processActionError(userActions.getAllUsers, e)
    }
  }

  const getUserById = async (userId: string) => {
    resetActionsInError()
    addToActionsInProgress(userActions.getUserById)

    try {
      const rsp = await apiClient.get(`/api/user/${userId}`)
      removeFromActionsInProgress(userActions.getUserById)
      return rsp.data.user
    }
    catch (e: unknown) {
      console.log(e)
      processActionError(userActions.getUserById, e)
    }
  }

  const editUser = async (editUserDTO: EditUserDTO): Promise<IUser | void> => {
    resetActionsInError()
    resetActionsInSuccess()
    addToActionsInProgress(userActions.editUser)

    try {
      const rsp = await apiClient.put(`/api/user/${editUserDTO.id}`, editUserDTO)
      /**
       * If the user being edited is the current user, we update the user
       * in the store.
       */
      if (rsp.data.user.id === user.value!.id) {
        storeSetUser(rsp.data.user)
      }
      processActionSuccess(userActions.editUser)
      await fetchAndStoreAlertsIndex()

      return rsp.data.user
    }
    catch (e: unknown) {
      console.log(e)
      processActionError(userActions.editUser, e)
    }
  }

  const getAllAbilities = async () => {
    resetActionsInError()
    addToActionsInProgress(userActions.getAllAbilities)

    try {
      const rsp = await apiClient.get('/api/abilities')

      removeFromActionsInProgress(userActions.getAllAbilities)
      return rsp.data.abilities
    }
    catch (e: any) {
      console.log(e)
      processActionError(userActions.getAllAbilities, e)
    }
  }

  const getAllRoles = async () => {
    resetActionsInError()
    addToActionsInProgress(userActions.getAllRoles)

    try {
      const rsp = await apiClient.get('/api/roles')

      removeFromActionsInProgress(userActions.getAllRoles)
      return rsp.data.roles
    }
    catch (e: any) {
      console.log(e)
      processActionError(userActions.getAllRoles, e)
    }
  }

  const getAllPermissions = async () => {
    resetActionsInError()
    addToActionsInProgress(userActions.getAllPermissions)

    try {
      const rsp = await apiClient.get('/api/permissions')

      removeFromActionsInProgress(userActions.getAllPermissions)
      return rsp.data.permissions
    }
    catch (e: any) {
      console.log(e)
      processActionError(userActions.getAllPermissions, e)
    }
  }

  const changeUserPassword = async (
    changeUserPasswordDTO: UpdateUserPasswordDTO,
    userId: string,
  ) => {
    resetActionsInError()
    addToActionsInProgress(userActions.changeUserPassword)

    const { password, passwordConfirmation } = changeUserPasswordDTO

    try {
      await apiClient.put(`/api/user/${userId}/password`, {
        password,
        password_confirmation: passwordConfirmation,
      })
      removeFromActionsInProgress(userActions.changeUserPassword)
      processActionSuccess(userActions.changeUserPassword)
    }
    catch (e: any) {
      console.log(e)
      processActionError(userActions.changeUserPassword, e)
    }
  }

  const addEmploymentHistory = async (userId: string): Promise<IUser | void> => {
    resetActionsInError()
    addToActionsInProgress(userActions.addEmploymentHistory)

    try {
      const rsp = await apiClient.post(`/api/user/${userId}/employment-history`)
      removeFromActionsInProgress(userActions.addEmploymentHistory)
      processActionSuccess(userActions.addEmploymentHistory)
      return rsp.data.user
    }
    catch (e: any) {
      console.log(e)
      processActionError(userActions.addEmploymentHistory, e)
    }
  }

  const updateEmploymentHistory = async (
    updateEmploymentHistoryFormData: any,
    userId: string,
  ): Promise<IUser | void> => {
    resetActionsInError()
    addToActionsInProgress(userActions.updateEmploymentHistory)

    try {
      const rsp = await apiClient.put(`/api/user/${userId}/employment-history`, {
        employmentLogs: updateEmploymentHistoryFormData,
      })
      removeFromActionsInProgress(userActions.updateEmploymentHistory)
      processActionSuccess(userActions.updateEmploymentHistory)
      return rsp.data.user
    }
    catch (e: any) {
      console.log(e)
      processActionError(userActions.updateEmploymentHistory, e)
    }
  }

  const getUpcomingShifts = async (userId: string, operationalBaseId: number | null): Promise<Shift[] | void> => {
    resetActionsInError()
    addToActionsInProgress(userActions.getUpcomingShifts)

    try {
      const rsp = await apiClient.get(`/api/v1/users/${userId}/upcoming`, {
        params: {
          operationalBase: operationalBaseId,
        },
      })
      removeFromActionsInProgress(userActions.getUpcomingShifts)
      return rsp.data.data
    }
    catch (e: unknown) {
      console.log(e)
      processActionError(userActions.getUpcomingShifts, e)
    }
  }

  return {
    user,
    userActions,
    createUser,
    fetchAndStoreUsersIndex,
    getAllAbilities,
    getAllRoles,
    getAllPermissions,
    getUserById,
    editUser,
    getActionSuccessMessage,
    storeResetUsers,
    changeUserPassword,
    updateEmploymentHistory,
    addEmploymentHistory,
    getUpcomingShifts,
  }
}
