import { queryOptions, useQuery } from '@tanstack/vue-query'
import { paramsAnd, trimKey } from '../lib'
import {
  type GetOperationalBasesByDepartmentIdKeyParams,
  type GetOperationalBasesByDepartmentIdsKeyParams,
  getOperationalBasesByDepartmentId,
  getOperationalBasesByDepartmentIds,
} from './api'

const entity = 'operational-bases'
const Scopes = {
  ByDepartmentIds: 'by-department-ids',
  ByDepartmentId: 'by-department-id',
} as const

export const keys = {
  byDepartmentIds: (
    params: GetOperationalBasesByDepartmentIdsKeyParams,
  ) => [{ entity, scope: Scopes.ByDepartmentIds, ...params }] as const,
  byDepartmentId: (
    params: GetOperationalBasesByDepartmentIdKeyParams,
  ) => [{ entity, scope: Scopes.ByDepartmentId, ...params }] as const,
} as const

export {
  keys as operationalBaseKeys,
  Scopes as operationalBaseScopes,
  keys as operationalBaseEntity,
}

export function useOperationalBasesByDepartmentIdsQuery(
  params: GetOperationalBasesByDepartmentIdsKeyParams,
) {
  return queryOptions({
    queryKey: keys.byDepartmentIds(params),
    queryFn: ({ queryKey }) => getOperationalBasesByDepartmentIds(trimKey(queryKey)),
    enabled: paramsAnd(params),
  })
}

export function useOperationalBasesByDepartmentIds(
  params: GetOperationalBasesByDepartmentIdsKeyParams,
) {
  return useQuery(useOperationalBasesByDepartmentIdsQuery(params))
}

export function useOperationalBasesByDepartmentIdQuery(
  params: GetOperationalBasesByDepartmentIdKeyParams,
) {
  return queryOptions({
    queryKey: keys.byDepartmentId(params),
    queryFn: ({ queryKey }) => getOperationalBasesByDepartmentId(trimKey(queryKey)),
    enabled: paramsAnd(params),
  })
}

export function useOperationalBasesByDepartmentId(
  params: GetOperationalBasesByDepartmentIdKeyParams,
) {
  return useQuery(useOperationalBasesByDepartmentIdQuery(params))
}
