import { ref } from 'vue'
import { defineStore } from 'pinia'
import type { IAlerts } from '@/shared/types/alerts/IAlerts'

export const useAlertsStore = defineStore('alerts', () => {
  const alertsIndex = ref<IAlerts | null>(null)

  function setAlertsIndex(i: IAlerts) {
    alertsIndex.value = i
  }

  function resetAlertsStore() {
    alertsIndex.value = null
  }

  return {
    setAlertsIndex,
    alertsIndex,
    resetAlertsStore,
  }
})
